          + '[3]  Le site « what song » n’en recense pas moins de 56 ! Url : https://www.what-song.com/Movies/Soundtrack/68/Casino. \n'
          + '[4]  Régis Dubois, Martin Scorsese. L’Infltré. Paris, Nouveau Monde Editions, 2019, p. 31.\n'
          + '[5] Martin Scorsese, « I Said Marvel Movies Aren’t Cinema. Let Me Explain. ». The New York Times, 4 novembre 2019.  Url : https://www.nytimes.com/2019/11/04/opinion/martin-scorsese-marvel.html.\n'
